<template>
  <div class="profile-front">
    <div class="profile-header">
      <img
        class="profile-header-image"
        src="../assets/denhi-profile-picture.jpg">
      <div class="profile-header-text-container">
        <h1>Denhi Huynh</h1>
        <p>Fullstack web developer - Food lover - Calisthenics noob - Boardgame geek</p>
        <hr>
      </div>
    </div>
    <div class="profile-content">
      <p class="profile-content-text">
        {{ getGreeting() }}! I'm Denhi, a fullstack software developer passionate about software development and web technologies. I enjoy working in a team and I am always ready to learn new technologies and share my own knowledge.
      </p>
    </div>
    <div class="profile-footer">
      <a
        href="https://www.linkedin.com/in/denhihuynh/"
        target="_blank"
        rel="noopener noreferrer"><i class="fab fa-linkedin linked-in-logo"/></a>
      <div><i class="fas fa-envelope email-icon" />denhi.huynh&#64;gmail.com</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProfileCardFront',
  methods: {
    getGreeting() {
      const date = new Date()
      const currentHour = date.getHours()
      let greetingPhrase = 'Hello'
      if (currentHour >= 0 && currentHour < 6) {
        greetingPhrase = 'Good night'
      } else if (currentHour >= 6 && currentHour < 12) {
        greetingPhrase = 'Good morning'
      } else if (currentHour >= 12 && currentHour < 18) {
        greetingPhrase = 'Good afternoon'
      } else if (currentHour >= 18 && currentHour <= 23) {
        greetingPhrase = 'Good evening'
      }
      return greetingPhrase
    }
  }
}
</script>
<style scoped>
.profile-header {
  display: flex;
  flex-direction: row;
  padding: 20px;
  height: 140px;
}

h1 {
  margin: 0;
  font-weight: 300;
  font-size: 3rem;
  text-align: left;
}

p {
  margin: 0;
  font-weight: 300;
  text-align: left;
}

hr {
  clear: both;
  display: block;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 1px;
}

.profile-header-image {
  width: 100px;
  height: 100px;
  border: 2px solid #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 20px;
}

.profile-header-text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: calc(100% - 80px);
}

.profile-content {
  height: calc(100% - 200px);
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.profile-content-text {
  width: 450px;
  max-height: 100%;
  overflow: auto;
}

.profile-footer {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: calc(100% - 40px);
  height: 60px;
  padding: 20px;
}

.email-icon {
  margin-right: 10px;
}

.linked-in-logo {
  color: black;
  margin-right: 10px;
}
</style>



