<template>
  <transition
    name="profile"
    appear>
    <div class="flip-container">
      <div
        class="click-area-left"
        @click="turnCard" />
      <div
        class="click-area-right"
        @click="turnCard" />
      <div :class="['profile',{flipper:showBack}]">
        <ProfileCardFront />
        <ProfileCardBack />
      </div>
    </div>
  </transition>
</template>

<script>
import ProfileCardFront from './ProfileCardFront'
import ProfileCardBack from './ProfileCardBack'
export default {
  name: 'ProfileCard',
  components: {
    ProfileCardFront,
    ProfileCardBack
  },
  data() {
    return {
      showBack: false
    }
  },
  methods: {
    turnCard() {
      this.showBack = !this.showBack
    }
  }
}
</script>
<style scoped>
.profile-enter {
  opacity: 0;
  transform: scale(0.5, 0.5);
}

.profile-enter-to {
  opacity: 1;
  transform: scale(1, 1);
}

.profile-enter-active {
  transition: all 1s;
}

.click-area-left {
  width: 120px;
  height: 100%;
  position: absolute;
  cursor: pointer;
  z-index: 3;
}

.click-area-right {
  width: 120px;
  height: 100%;
  position: absolute;
  right: 0;
  cursor: pointer;
  z-index: 3;
}

.flip-container {
  position: relative;
  perspective: 1000;
  transition: 1s;
}

@media screen and (max-width: 700px) {
  .flip-container {
    transform: rotate(90deg);
    width: 95vh;
    height: 95vw;
  }

  .profile-front,
  .profile-back {
    width: 95vh;
    height: 95vw;
  }
}

@media screen and (min-width: 701px) {
  .flip-container,
  .profile-front,
  .profile-back {
    width: 700px;
    height: 500px;
  }
}

.flip-container .flipper {
  transform: rotateY(180deg);
  transition: 1s;
}

.flip {
  transform: rotateY(180deg);
  transition: 1s;
}

.profile-front,
.profile-back {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  border-radius: 5px;
  box-shadow: 2px 2px 1px 1px rgba(0, 0, 0, 0.2);
  padding: 20px;
}

.profile-front {
  z-index: 2;
  position: relative;
}

.profile-back {
  transform: rotateY(180deg);
  transition: 1s;
}

.profile {
  perspective: 1000;
  transition: 1s;
  transform-style: preserve-3d;
  position: relative;
}
</style>



